.basics{
    font-family: "Calibre","Inter","San Francisco","SF Pro Text",-apple-system,system-ui,sans-serif;
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content: center;
    margin-top: 30vh;
    height: 40vh;
    width: 80vw;
    margin-left: 2vw;
    padding-left: 3vw;
    padding-right: 10vw;
   
}

.extra{
    padding-right: 30vw;
}

.style{
    font-family: "SF Mono,Fira Code","Fira Mono","Roboto Mono",monospace;
}


@media screen and (max-width: 600px) {
    .basics{
        display: flex;
        flex-direction: column;
        align-self: center;
        justify-content: center;
        margin-top: 30vh;
        height: 40vh;
        width: 100vw;
        margin-left: 0vw;
        padding-left: 5vw;
        padding-right: 10vw;
    }

    .extra{
        padding-right: 5vw;
    }
}