.base1{
    font-family: "Calibre","Inter","San Francisco","SF Pro Text",-apple-system,system-ui,sans-serif;
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content: start;
    margin-top: 30vh;
    height: 270vh;
    width: 80vw;
    margin-left: 2vw;
    padding-left: 10vw;
    padding-right: 10vw;
   
}

.numbering{
    display: flex;
    justify-content: start;
    align-items: center;
    
}





@media screen and (max-width: 600px) {
    .base1{
        font-family: "Calibre","Inter","San Francisco","SF Pro Text",-apple-system,system-ui,sans-serif;
        display: flex;
        flex-direction: column;
        align-self: center;
        justify-content: start;
        margin-top: 100vh;
        height: 250vh;
        
        width: 100vw;
        margin-left: 0vw;
        padding-left: 2vw;
        padding-right: 2vw;
        
       
    }




 


}

