.base{
    font-family: "Calibre","Inter","San Francisco","SF Pro Text",-apple-system,system-ui,sans-serif;
    display: flex;
    flex-direction: column;
    align-self: center;
    justify-content: start;
    margin-top: 30vh;
    height: 60vh;
    width: 80vw;
    margin-left: 2vw;
    padding-left: 10vw;
    padding-right: 10vw;
   
}

.numbering{
    display: flex;
    justify-content: start;
    align-items: center;
    
}


.filter{
    transform: translate(-1vw,-1vw);
    
}

.filter{
    filter: none;
    transform: translate(-1vw,-1vw) scale(1.05);
    
}



@media screen and (max-width: 600px) {
    .base{
        font-family: "Calibre","Inter","San Francisco","SF Pro Text",-apple-system,system-ui,sans-serif;
        display: flex;
        flex-direction: column;
        align-self: center;
        justify-content: start;
        margin-top: 30vh;
        height: 90vh;
        max-height: 90vw;
        width: 90vw;
        margin-left: 0vw;
        padding-left: 0vw;
        padding-right: 0vw;
       
    }


    .filter{
        transform: translate(-3vw,-3vw);
        transition: all 0.9s ease-in-out;
    }
    
    .filter:hover{
        filter: none;
        transform: translate(-3vw,-3vw) scale(1.05);
        
    }


}

img{
    opacity: 0.6;
    transition: all 0.9s ease-in-out;
}

img:hover{
    opacity: 1;
    transform: scale(1.05);
}


.card{
    background: #112240;
    transform: translate(2vw);
}


.shift{
    margin-left: 2vw;
}